"use client"

import { useState } from "react"
import Image from "next/image"
import { Modal } from "./modal"

interface GalleryItem {
  id: number
  src: string
  caption: string
}

interface GalleryGridProps {
  items: GalleryItem[]
}

export function GalleryGrid({ items }: GalleryGridProps) {
  const [selectedImage, setSelectedImage] = useState<GalleryItem | null>(null)

  return (
    <>
      <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 gap-4">
        {items.map((item) => (
          <button
            key={item.id}
            onClick={() => setSelectedImage(item)}
            className="relative aspect-[4/3] rounded-lg overflow-hidden shadow-md hover:shadow-xl transition-shadow cursor-pointer group border border-gray-200"
          >
            <Image
              src={item.src || "/placeholder.svg"}
              alt={item.caption}
              fill
              className="object-cover group-hover:scale-105 transition-transform duration-300"
            />
            <div className="absolute inset-0 bg-black/0 group-hover:bg-black/30 transition-colors" />
            <div className="absolute bottom-0 left-0 right-0 p-3 bg-black/70 opacity-0 group-hover:opacity-100 transition-opacity">
              <p className="text-white text-sm line-clamp-2">{item.caption}</p>
            </div>
          </button>
        ))}
      </div>

      <Modal
        isOpen={!!selectedImage}
        onClose={() => setSelectedImage(null)}
        image={selectedImage?.src || ""}
        caption={selectedImage?.caption || ""}
      />
    </>
  )
}
